.user {
  display: flex;
  align-items: center;
  margin-bottom: 2rem;
}

.action {
  margin-right: 2rem;
}

.pic {
  display: block;
  margin-bottom: 1rem;
  width: 60px;
  height: 60px;
  object-fit: cover;
  border-radius: 50%;
}

.info {
  display: flex;
  padding: 1rem;
  justify-content: space-between;
  border: 1px solid #dedede;
  flex: 1 1 auto;
  border-radius: 4px;

  h3 {
    margin: 0;
  }
}

